@font-face {
  font-family: "panton";
  src: url(../fonts/Panton-BlackCaps.otf);
}
.carrito__vaciar, .carrito__vaciar:hover {
  border-radius: 1rem;
  border-top: 1px solid #ff7d7d;
  background-color: #f05454;
  padding: 1.1rem 2.1rem;
  font-family: "Inter", sans-serif;
  font-size: 2rem;
  color: #ffffff;
  font-weight: 600;
  cursor: pointer;
  transition-property: background-color, border-top;
  transition-duration: 0.2s;
  transition-timing-function: ease;
}
.carrito__vaciar::selection, .carrito__vaciar:hover::selection {
  background: transparent;
}
.carrito__vaciar:hover {
  border-top: 1px solid #e97171;
  background-color: #d04b4b;
}
.carrito__vaciar:active {
  border-top: 1px solid #d75d5d;
  background-color: #bc3838;
}

.carrito {
  position: fixed;
  margin-top: 1rem;
  right: 20rem;
  top: 10rem;
  z-index: 3;
  backdrop-filter: blur(1rem);
  background: rgba(48, 71, 94, 0.5);
  padding: 2rem;
  border-radius: 2rem;
  border: 1px solid #7a99c8;
}
.carrito__img {
  width: 10rem;
  border: 1px solid #ffffff;
  border-radius: 1rem;
}
.carrito tr {
  font-family: "Inter", sans-serif;
}
.carrito tr th,
.carrito tr td {
  color: #ffffff;
  text-align: center;
}
.carrito tr th {
  padding: 0 2rem 2rem 2rem;
}
.carrito__cesta {
  margin-top: 2rem;
  border-top: 1px solid #7a99c8;
}
.carrito__total {
  font-size: 3rem;
  margin: 1rem 0;
  font-weight: 600;
}
.carrito__vaciar {
  display: block;
  text-align: center;
}

.borrar-curso {
  font-family: "panton";
  font-size: 3rem;
  display: block;
  background-color: #f05454;
  transform: rotate(45deg);
  border-radius: 50%;
  width: 3rem;
  height: 3rem;
}

.navegacion__btn {
  border-radius: 1rem;
  border-top: 1px solid #ff7d7d;
  background-color: #f05454;
  padding: 0.7rem 1.5rem;
  font-family: "Inter", sans-serif;
  font-size: 2rem;
  color: #ffffff;
  font-weight: 500;
  cursor: pointer;
  transition-property: background-color, border-top;
  transition-duration: 0.2s;
  transition-timing-function: ease;
}
.navegacion__btn::selection {
  background: transparent;
}
.navegacion__btn:hover {
  border-top: 1px solid #e97171;
  background-color: #d04b4b;
}
.navegacion__btn:active {
  border-top: 1px solid #d75d5d;
  background-color: #bc3838;
}

.navegacion {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  border-top: 1px solid #46617c;
  background: rgba(48, 71, 94, 0.5);
  box-shadow: -12px 12px 42px 1px rgba(0, 0, 0, 0.82);
  position: fixed;
  z-index: 3;
  left: 0;
  right: 0;
  top: 2rem;
  max-width: 120rem;
  width: 90%;
  margin: 0 auto;
  backdrop-filter: blur(2rem);
  border-radius: 2rem;
}
.navegacion__items {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 5rem;
  position: absolute;
  right: 0;
  text-align: center;
  border-radius: 2rem;
  padding: 4rem;
  box-shadow: -12px 12px 42px 1px rgba(0, 0, 0, 0.82);
  background-color: #222831;
  background-image: url(../img/islaNav.png);
  background-repeat: no-repeat;
  background-position: right bottom;
  background-size: 50%;
  top: 11rem;
  transform: translateX(100rem);
  transition: transform 0.7s cubic-bezier(0.77, -0.19, 0.29, 1.16);
}
.navegacion__items--active {
  transform: translateX(0);
}
@media screen and (min-width: 1024px) {
  .navegacion__items {
    position: relative;
    top: 0;
    flex-direction: row;
    align-items: center;
    border-radius: 0;
    padding: 0;
    transform: translateY(0);
    box-shadow: 0 0 0;
    background-color: transparent;
    background-image: none;
  }
}
.navegacion__item {
  transition: color 0.3s ease;
}
.navegacion__item:hover {
  color: #7a99c8;
}
.navegacion__li--btns {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}
.navegacion__li {
  position: relative;
}
.navegacion__numero--articulos {
  position: absolute;
  bottom: 0;
  right: 0;
  background-color: #f05454;
  width: 2rem;
  height: 2rem;
  text-align: center;
  margin: 0;
  border-radius: 50%;
  font-family: "panton";
}
@media screen and (min-width: 1024px) {
  .navegacion__numero--articulos {
    font-size: 1.5rem;
  }
}
.navegacion__interacciones {
  display: flex;
  gap: 1rem;
}
.navegacion__interacciones i {
  color: #ffffff;
  font-size: 4rem;
}
.navegacion__interacciones i:first-child:hover + .carrito {
  opacity: 1;
}

@media screen and (min-width: 1024px) {
  .bx.bx-menu {
    display: none;
  }
}

.header__btn {
  border-radius: 1rem;
  border-top: 1px solid #9cbdef;
  background-color: #7a99c8;
  letter-spacing: 0.1rem;
  padding: 1.1rem 2.1rem;
  font-family: "panton";
  color: #ffffff;
  font-size: 2rem;
  transition-property: background-color, border-top;
  transition-duration: 0.2s;
  transition-timing-function: ease;
}
.header__btn::selection {
  background: transparent;
}
.header__btn:hover {
  background: #6688bd;
  border-top: 1px solid #7ea0d5;
}
.header__btn:active {
  background: #5978a6;
  border-top: 1px solid #7497cc;
}

.header {
  height: 155vh;
  width: 100%;
  background-image: url(../img/headerMovil.png);
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}
@media screen and (min-width: 768px) and (max-width: 1023px) {
  .header {
    height: 100vh;
  }
}
@media screen and (min-width: 1024px) {
  .header {
    height: 120vh;
    background-image: url(../img/imgHeader.svg);
    background-position: center center;
  }
}
.header__contenido {
  padding-top: 15rem;
}
@media screen and (min-width: 768px) and (max-width: 1023px) {
  .header__contenido {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 5rem;
    align-items: center;
  }
}
@media screen and (min-width: 1024px) {
  .header__contenido {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    align-items: center;
  }
}
.header__heading {
  text-align: center;
  line-height: 1.5;
}
@media screen and (min-width: 768px) and (max-width: 1023px) {
  .header__heading {
    text-align: left;
    line-height: 1.5;
  }
}
.header__heading--color {
  color: #7a99c8;
}
.header__card {
  margin-top: 5rem;
  background-color: #222831;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  border-radius: 2rem;
  gap: 3rem;
  align-items: center;
  box-shadow: 1px 0px 46px 5px rgba(0, 0, 0, 0.75);
  border-top: 1px solid #3d5163;
}
@media screen and (min-width: 768px) and (max-width: 1023px) {
  .header__card {
    margin-top: 0;
    align-items: flex-start;
  }
}
@media screen and (min-width: 1024px) {
  .header__card {
    margin-top: 0;
    align-items: flex-start;
    position: relative;
    z-index: 2;
  }
}
.header__info {
  text-align: center;
  margin: 0;
}
@media screen and (min-width: 768px) and (max-width: 1023px) {
  .header__info {
    text-align: left;
  }
}
@media screen and (min-width: 1024px) {
  .header__info {
    text-align: left;
  }
}
.header__imagen {
  grid-column: 1/3;
  position: relative;
  z-index: 1;
  animation-name: islaFlotante;
  animation-iteration-count: infinite;
  animation-duration: 10s;
  animation-timing-function: cubic-bezier(0.57, -0.01, 0.47, 0.99);
}
.header__imagen img {
  margin-left: auto;
  margin-right: auto;
  filter: drop-shadow(-10px 10px 21px rgb(0, 0, 0));
  position: relative;
}
@media screen and (min-width: 768px) and (max-width: 1023px) {
  .header__imagen img {
    width: 60%;
    position: relative;
    top: -3rem;
  }
}
@media screen and (min-width: 1024px) {
  .header__imagen img {
    top: -15rem;
  }
}
@keyframes islaFlotante {
  0% {
    transform: translateY(1rem);
  }
  50% {
    transform: translateY(-1rem);
  }
  100% {
    transform: translateY(1rem);
  }
}

html {
  box-sizing: border-box;
